<template>
  <div v-loading="loading">
    <el-row>
      <el-card shadow="never">
        <div ref="viewStat" style="width: 100%;height: 450px">
        </div>
      </el-card>
    </el-row>
    <br>
    <el-row>
      <el-card shadow="never">
        <div ref="baseStat" style="width: 100%;height: 450px">
        </div>
      </el-card>
    </el-row>
  </div>
</template>

<script>

// 
import { mapState } from 'vuex'

const echarts = require('echarts/lib/echarts')
// 
require('echarts/lib/chart/bar')
require('echarts/lib/chart/line')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')

export default {
  name: 'index',
  data () {
    return {
      loading: false
    }
  },
  methods: {
    init () {
      this.loadBaseStat()
      this.loadViewStat()
    },
    async loadBaseStat () {
      this.loading = true
      const result = await this.$request.fetchBaseStat()
      if (result.code) {
        this.loading = false
        return
      }
      const chart = echarts.init(this.$refs.baseStat)
      // 
      chart.setOption({
        title: { text: '' },
        tooltip: {},
        xAxis: {
          data: ['', '', '', '', '']
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          data: Object.values(result.data)
        }]
      })
      this.loading = false
    },
    async loadViewStat () {
      this.loading = true
      const result = await this.$request.fetchViewStat()
      if (result.code) {
        this.loading = false
        return
      }
      const chart = echarts.init(this.$refs.viewStat)
      // 
      chart.setOption({
        title: { text: '' },
        tooltip: {},
        xAxis: {
          type: 'category',
          data: result.data.days
        },
        yAxis: {
          type: 'value'
        },
        series: [{
          name: '',
          type: 'line',
          data: result.data.counts
        }]
      })
      this.loading = false
    }
  },
  computed: {
    ...mapState(['dict'])
  }
}
</script>
